body {
  background-color: #f3f3f3;
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

.container {
  max-width: 600px;
  margin: 0 auto;
  padding: 20px;
}

h1, .loader-container {
  text-align: center;
  margin-bottom: 20px;
}

.loader {
  width: 40px;
  height: 40px;
  border: 4px solid #ccc;
  border-top-color: #333;
  border-radius: 50%;
  animation: loader-spin 1s ease-in-out infinite;
  display: inline-block;
}

@keyframes loader-spin {
  to {
    transform: rotate(360deg);
  }
}

.error {
  color: red;
  text-align: center;
  margin-bottom: 20px;
}

.hidden {
  display: none;
}

.select-container {
  margin-bottom: 20px;
}

.breed-select {
  width: 100%;
  border: 1px solid #ccc;
  border-radius: 5px;
  padding: 10px;
  font-size: 16px;
  overflow-y: scroll;
}

.cat-info {
  background-color: #fff;
  border-radius: 5px;
  padding: 20px;
}

.cat-name {
  margin-top: 0;
  font-size: 24px;
}

.cat-description {
  margin-bottom: 10px;
}

.cat-temperament {
  color: #777;
  margin-bottom: 20px;
  font-style: italic;
}

.cat-image {
  max-width: 100%;
  height: auto;
  border-radius: 5px;
  margin: 0 auto;
  display: block;
}

